<template>
  <a-row type="flex" justify="end"  style="min-width: 1400px">
    <a-col style="width: 85%">
      <div>
        <div class="mlist-control ant-list-bordered">
          <div class="list-control-finish" style="display: flex;flex-direction: row;justify-content: space-between;align-items: center;height: 100%;width: 95%">
            <a-form layout="inline" @submit="handleSubmit" :form="form" >
              <a-form-item label="选择企业">
                <a-select default-value="全部" style="width: 200px" :value="enterName" @change="handleEnterNameChange">
                  <a-select-option value="全部">全部</a-select-option>
                  <a-select-option v-for="item in orderList" :key="item" :value="item.enterName">{{item.enterName}}</a-select-option>
                </a-select>
              </a-form-item>
              <a-form-item label="订单状态">
                <a-select default-value="全部" style="width: 200px" :value="needsPayStatus" @change="handleTypeChange">
                  <a-select-option value="全部">全部</a-select-option>
                  <a-select-option value="待支付">待支付</a-select-option>
                  <a-select-option value="交易成功">交易成功</a-select-option>
                </a-select>
              </a-form-item>
            </a-form>
            <a-button type="primary" @click="exportExcel" style="margin-left: auto">下载当前订单</a-button>
          </div>
        </div>

        <a-list
          class="listpage"
          :bordered="true"
          :pagination="paginationProps"
          :loading="loading"
          itemLayout="horizontal"
          :dataSource="data"
          :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }"
        >
          <a-list-item slot="renderItem" slot-scope="item">
            <a-button type="primary" slot="actions" @click="getDemandDetail(item.id)">查看</a-button>

            <a-list-item-meta>
              <p slot="title">{{item.needsFarmerType}} 工种</p>
              <a-avatar slot="avatar" shape="square" style="width:50px;height:50px;padding-top: 25px;" :style="machineStatusStyleAvatar(item.needsPayStatus)" ></a-avatar>

            </a-list-item-meta>
            <span class="ant-list-item-meta-description" style="width:100px;margin-right:20px;display:inline-block;align-self:center">
              {{ item.enterName }}
            </span>
            <span class="ant-list-item-meta-description" style="width:60px;margin-right:50px">
              订单数量
              <br />
              {{ item.orderNum }}
            </span>
            <span class="ant-list-item-meta-description" style="width:100px;margin-right:50px">
              订单金额
              <br />
              {{ item.orderMoneyToFarmers }}
            </span>
            <span class="ant-list-item-meta-description" style="width:100px;margin-right:50px">
              订单总额
              <br />
              {{ item.orderMoney }}
            </span>
            <span
              :style="machineStatusStyle(item.needsPayStatus)"
              class="ant-list-item-meta-description"
              style="display:inline-block;width:100px;align-self:center"
            >{{ item.needsPayStatus }}</span>
          </a-list-item>

          <br />
        </a-list>

        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { getAllOrders } from "../../api/manRequest";
//import reqwest from 'reqwest'

export default {
  data() {
    return {
      id: localStorage.getItem("userId"),
      loading: false,
      loadingMore: false,
      showLoadingMore: true,
      enterName: "全部",
      needsPayStatus: "全部",
      orderList: [],
      filterList: []
    };
  },
  created() {
    this.$parent.showSiderMenu = true;
    this.$parent.showMenu = false;
  },
  computed: {
    list() {
      // return this.$store.state.taskList;
    },
    paginationProps() {
      let pag = {
        pageSize: 6,
        total: this.data.length
      };
      return pag;
    },
    data() {
      let list = [...this.orderList];
      if (this.needsPayStatus === "待支付") {
        list = list.filter(item => item.needsPayStatus == "待支付");
      } else if (this.needsPayStatus === "交易成功") {
        list = list.filter(item => item.needsPayStatus == "交易成功");
      }
      if (this.enterName != "全部") {
        list = list.filter(item => item.enterName == this.enterName)
      }
      return list;
    }
  },
  mounted() {
    // this.$store.dispatch("gettaskList");
    this.getOrdersList();
    // this.getData((res) => {
    //   this.loading = false
    //   this.data = res.results
    // })
  },
  methods: {
    // 动态样式
    machineStatusStyleAvatar(e){
      if(e == '待支付'){
        return {
          'background-color':'#CF9137'
        }
      } else if(e == '交易成功'){
        return {
          'background-color':'#206648'
        }
      };
    },
    machineStatusStyle(e){
      if(e == '待支付'){
        return {
          'color':'#D39846'
        }
      } else if(e == '交易成功'){
        return {
          'color':'#206648'
        }
      };
    },
    // 选择
    handleTypeChange(e) {
      this.needsPayStatus = e;
    },
    handleEnterNameChange(e) {
      this.enterName = e;
    },
    getOrdersList() {
      getAllOrders()
        .then(response => {
          this.orderList = response.data.data;
          console.log(this.orderList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    onSearch() {},
    getDemandDetail(itemId) {
      this.$router.push({
        name: "ManOrderDetail",
        query: {
          id: itemId
        }
      });
    },
    exportExcel() {
            import('../../excle/Export2Excel').then(excel => { // 导入js模块
                const tHeader = ['编号', '企业名称', '需求名称', '工种','支付状态','订单数量','订单金额(支付给农民工)','订单总额(包含平台服务费))']; // 导出excel 的标题
                const filterVal = ['id','enterName', 'needDes', 'needsFarmerType','needsPayStatus','orderNum','orderMoney','orderMoneyToFarmers']; // 每个标题对应的字段

                const list = (this.orderList || []).map((item, key) => { // 通过 map 方法遍历，组装数据成上面的格式
                    return {
                        id: item.id,
                        enterName: item.enterName,
                        needDes: item.needDes,
                        needsFarmerType: item.needsFarmerType,
                        needsPayStatus: item.needsPayStatus,
                        orderNum: item.orderNum,
                        orderMoney: item.orderMoney,
                        orderMoneyToFarmers: item.orderMoneyToFarmers
                    };
                });

                if (list) {
                    const data = this.formatJson(filterVal, list); // 生成json数据
                    excel.export_json_to_excel( // 调用excel方法生成表格
                        tHeader,
                        data,
                        this.needsPayStatus + '订单信息'
                    );
                } else {
                    alert('暂无无数据');
                }
                // this.loading = false;
            })
        },
        formatJson (filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        }
  }
};
</script>
<style scoped>
.list-control {
  background: #fff;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  height: 60px;
  display: flex;
  flex-direction: row;
}
/* .list-control-info {
  display: inline-block;
  align-self: center;
  margin-right: 10px;
} */
:global(.list-control-finish) {
  height: 122px;
  margin-left: 50px;
}


.listpage {
  min-height: 500px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 40px 0px 40px;
  width: 90%;
  min-width: 1200px;
}
.Content {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
</style>



